import { Component, OnInit } from '@angular/core';
//import { NguCarousel } from '@ngu/carousel';
import { NguCarousel, NguCarouselStore } from '@ngu/carousel';
import { Router } from '@angular/router';

@Component({
  selector: 'app-service-carousel',
  templateUrl: './service-carousel.component.html',
  styleUrls: ['./service-carousel.component.scss']
})
export class ServiceCarouselComponent implements OnInit {

  router: any;
  url:string;
  n:any;
  result:any;
  events:[{
    url:'#services/cloud-native',
    name:'Cloud-Native Application Development'
  }]
  constructor(private _router: Router ) {
    this.router = _router;
    this.url = this.router.url;
    var n = this.url.lastIndexOf('/');
    this.result = this.url.substring(n + 1);
  }

  public carouselOne: NguCarousel;

  ngOnInit() {
    this.carouselOne = {
      grid: {xs: 1, sm: 2,md:3,lg: 3, all: 0},
      slide: 1,
      speed: 400,
      interval: 100000,
      point: {
        visible: false
      },
      load: 1,
      touch: true,
      loop: true,
      custom: 'banner'
    }
  }

  public myfunc(event: Event) {}

  onmoveFn(data: NguCarouselStore) {
  }

}
