import { ModuleWithProviders, NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { HomeComponent } from './home/home.component';
import { ProductComponent } from './product/product.component';
import { EnigmaComponent } from './product/enigma/enigma.component';
import { HerculisComponent } from './product/herculis/herculis.component';
import { SwordfishComponent } from './product/swordfish/swordfish.component';
import { CasestudyComponent } from './casestudy/casestudy.component';
import { PartnersComponent } from './partners/partners.component';
import { SignupComponent } from './signup/signup.component';
import { ContactPageComponent } from './contact-page/contact-page.component';

import { SuitsComponent } from './common/suits/suits.component';
import { FeaturesComponent } from './features/features.component';
import { ServiceComponent } from './service/service.component';
import { CloudNativeComponent } from './service/cloud-native/cloud-native.component';
import { ApplicationComponent } from './service/application/application.component';
import { DockerContainerComponent } from './service/docker-container/docker-container.component';
import { EnterpriseDevOpsComponent } from './service/enterprise-dev-ops/enterprise-dev-ops.component';
import { InfrastructureComponent } from './service/infrastructure/infrastructure.component';
import { DevSecOpsComponent } from './service/dev-sec-ops/dev-sec-ops.component';
import { CloudMigrationComponent } from './service/cloud-migration/cloud-migration.component';
import { MicroservicesComponent } from './service/microservices/microservices.component';

export const router: Routes = [
  {
    path: '',
    redirectTo: '/',
    pathMatch: 'full'
  },
  {
    path: '',
    component: HomeComponent
  },
  // { path: '**', component: HomeComponent },
  {
    path: 'product',
    component: ProductComponent
  },

  { path: 'product/enigma', component: EnigmaComponent },

  { path: 'product/swordfish', component: SwordfishComponent },

  { path: 'product/hercules', component: HerculisComponent },

  {
    path: 'casestudy',
    component: CasestudyComponent
  },

  { path: 'partners', component: PartnersComponent },

  {
    path: 'signup',
    component: SignupComponent
  },

  {
    path: 'contact',
    component: ContactPageComponent
  },

  { path: 'suits', component: SuitsComponent },
  { path: 'features', component: FeaturesComponent },
  { path: 'services', component: ServiceComponent },
  { path: 'services/cloud-native', component: CloudNativeComponent },
  {
    path: 'services/application-modernization',
    component: ApplicationComponent
  },
  { path: 'services/dockerandcontainer', component: DockerContainerComponent },
  { path: 'services/enterpriseDevops', component: EnterpriseDevOpsComponent },
  { path: 'services/infrastructure', component: InfrastructureComponent },
  { path: 'services/devSecOpsandSecurity', component: DevSecOpsComponent },
  { path: 'services/cloud-migration', component: CloudMigrationComponent },
  { path: 'services/microservices', component: MicroservicesComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(router)],
  exports: [RouterModule]
})
export class AppRoutingModule {}
